body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.score-board{display:flex;justify-content:space-between;align-items:center;border:2px solid #606e85;border-radius:0.5rem;width:100%;padding:0.75rem;transition:all 0.2s ease-in-out;position:relative;margin-bottom:2rem}@media (min-width: 992px){.score-board{width:50%}}.score-card{border-radius:0.5rem;display:flex;flex-direction:column;align-items:center;justify-content:space-between;background-color:white;padding:0.5rem;height:100%;min-width:33%}.score-card-title{color:#2a46c0;font-size:1.5rem}.score-card-score{color:#3b4363;font-weight:700;font-size:4rem}

.game-pick,.game-piece{position:absolute;background-color:white;border:1rem solid #4865f4;border-radius:100%;box-shadow:inset 1px 8px 5px #606e8570;padding:1rem;display:flex;justify-content:center;align-items:center;height:8rem;width:8rem;transition:all ease-in 0.5s}.game-pick:hover{-webkit-filter:brightness(120%) saturate(150%);filter:brightness(120%) saturate(150%)}.game-pick:not(.scissors):hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.game-piece{position:relative}

.game-result{position:relative;width:100%;margin-bottom:2rem}.game-result-winlose{font-size:4rem;font-weight:700}.game-result-play{font-size:1.5rem;background-color:white;border-radius:0.5rem;color:#3b4363;padding:0.5rem 3rem;transition:all 0.2s ease-in-out}@media (min-width: 992px){.game-result-CTA{position:absolute !important;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}}.game-result-pick,.game-result-CTA{position:relative;display:flex;flex-direction:column;align-items:center;justify-content:space-between;padding:1rem 0}.game-result-you-picked:after,.game-result-house-picked:after{content:"";position:absolute;bottom:2.5rem;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);font-size:1.5rem}@media (min-width: 576px){.game-result-you-picked:after,.game-result-house-picked:after{font-size:1.3rem;top:2.5rem}}@media (min-width: 768px){.game-result-you-picked:after,.game-result-house-picked:after{font-size:1.5rem}}.game-result-you-picked:after{content:"you picked"}@media (min-width: 992px){.game-result-player{display:flex}}@media (min-width: 992px){.game-result-house{display:flex;justify-content:flex-end}}.game-result-house-picked:after{content:"house picked"}.game-result-pick-winner,.game-result-pick-looser{position:relative;display:flex;justify-content:center;align-items:center;overflow:visible;border-radius:100%;width:260px;height:260px}@media (min-width: 576px){.game-result-pick-winner,.game-result-pick-looser{width:242px;height:242px}}@media (min-width: 768px){.game-result-pick-winner,.game-result-pick-looser{width:332px;height:332px}}@media (min-width: 992px){.game-result-pick-winner,.game-result-pick-looser{width:370px;height:370px}}.game-result-pick-winner{background-image:repeating-radial-gradient(closest-side ellipse at 50% 50%, #1f3756 0px 0.5rem, #141539 3rem)}

.game-picks{position:relative;width:25rem;min-height:23rem;-webkit-transform:scale(60%);transform:scale(60%);transition:all 0.2s ease-in-out;margin-bottom:2rem}@media (min-width: 440px){.game-picks{-webkit-transform:scale(100%);transform:scale(100%);min-height:26rem}}.game-picks:before{content:"";background-image:url(/rock-paper-scissors-lizard-spock/static/media/bg-pentagon.1ad6d365.svg);position:absolute;top:50%;left:50%;height:20rem;width:21rem;background-repeat:no-repeat;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.lizard{border-color:#834fe3;box-shadow:inset 1px 8px 5px rgba(96,110,133,0.439216),1px 8px 1px #2a1353}.lizard-position{bottom:0;left:40px}.rock{border-color:#dc2e4e;box-shadow:inset 1px 8px 5px rgba(96,110,133,0.439216),1px 8px 1px #5a1622}.rock-position{bottom:0;right:40px}.spock{border-color:#40b9ce;box-shadow:inset 1px 8px 5px rgba(96,110,133,0.439216),1px 8px 1px #2b717d}.spock-position{top:6rem;left:0.5rem}.paper{border-color:#4865f4;box-shadow:inset 1px 8px 5px rgba(96,110,133,0.439216),1px 8px 1px #1633c0}.paper-position{top:6rem;right:0.5rem}.scissors{border-color:#eca922;box-shadow:inset 1px 8px 5px rgba(96,110,133,0.439216),1px 8px 1px #845b10}.scissors-position{top:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.scissors-position:hover{-webkit-transform:scale(1.1) translateX(-50%);transform:scale(1.1) translateX(-50%)}

.game-rules{position:absolute;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,0.5)}.game-rules-content{border-radius:1rem;position:absolute;top:0;left:0;width:100%;height:100%;display:flex;flex-direction:column;justify-content:space-between;align-items:center;padding:20% 0 1rem;height:100vh}@media (min-width: 375px){.game-rules-content{top:5rem;left:50%;width:auto;height:auto;-webkit-transform:translateX(-50%);transform:translateX(-50%)}}@media (min-width: 375px){.game-rules-content{padding:1rem;height:auto}}@media (min-width: 375px){.game-rules-content header{align-self:flex-start}}.game-rules-content header h2{color:#3b4363;font-size:4rem;font-weight:700}@media (min-width: 375px){.game-rules-content header h2{font-size:2rem;font-weight:400}}.game-rules-content main img{width:100%}@media (min-width: 375px){.game-rules-close{position:absolute;top:1.5rem;right:1rem}}

.App{background-color:#1f3756;color:white;font-family:"Barlow Semi Condensed",sans-serif;font-size:1rem;padding:1rem 0;text-transform:uppercase}@media (min-width: 992px){.App{height:100vh}}.App-container{display:flex;flex-direction:column;align-items:center;justify-content:space-between}.App .game-rules-btn{font-size:1.5rem;border:1px solid white;border-radius:0.5rem;padding:0.5rem 3rem;transition:all 0.2s ease-in-out}@media (min-width: 992px){.App .game-rules-btn{align-self:flex-end}}

